<template>
    <section class="index">
        <button @click.prevent="prev" class="btn-1"></button>
    </section>
</template>

<script>
    export default {
        name: "v-user-index" ,
        methods: {
            prev () {
                // console.log('' , this.$store.state.trans);
                this.$store.commit('back');
            } ,
            user () {
                this.$store.commit('routerNext' , '/user/index');
            }

        }
    }
</script>

<style scoped>
    .index {
        width:100%;
        height:80px;
        background-color:gray;
    }
</style>